	module.exports = {"data":{"wordpressPost":{"title":"James Franco Is The New Face of Coach For Men And We&#8217;re Pretty Happy","content":"<p>Oh that<strong> James Franco</strong>. You know what we&#8217;re saying here right?</p>\n<p>No? Let us break it down for you. The man is an actor, director, writer, artist and philanthropist. He&#8217;s creative, confident, curious, dynamic, daring, refined and thoughtful. Oh, and one more thing &#8211; he is SUPER hot. Not that we care or anything &#8230; ahem.</p>\n<p>What we do care about is keeping you up to date on all that&#8217;s happening in the world of beauty. So here&#8217;s some news for you &#8211; James is the new face of the fragrance <strong>Coach for Men</strong>. And we have to say, he&#8217;s the perfect choice. The folk over at Coach clearly think so too.</p>\n<div class=\"page\" title=\"Page 1\">\n<div class=\"layoutArea\">\n<div class=\"column\">\n<p>“James is the quintessential-cool Coach guy,&#8221; says Coach creative director Stuart Vevers.</p>\n<p>&#8220;He’s handsome, there’s a bit of danger and he’s thoughtful and challenging, energetic and prolific.&#8221;</p>\n<p>See! Even Stuart thinks he&#8217;s handsome! But back to the news. The fragrance will be launched in September this year. It will join the women&#8217;s fragrance Coach Eau De Parfum, which launched in September, 2016. While we&#8217;re yet to discover what the men&#8217;s scent will smell like, we can tell you the women&#8217;s version is heavenly.</p>\n<p>The <a href=\"http://the-beautyinsider.com/perfume-will-make-man-fall-madly-love/\">floriental fruity blend</a> is said to have been inspired by the constant thrum and beat of New York City. it opens with sparkling raspberry, which gives way to creamy Turkish roses and ends on a suede musk <a href=\"http://the-beautyinsider.com/perfume-will-make-man-fall-madly-love/\">base note</a>. The flacon features a spray cap shaped like a gold turncock, imitating the iconic clasp of Coach&#8217;s most famed handbags.</p>\n<img class=\"size-medium wp-image-2164 alignnone\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/26223103/COACH_EDP_50ml-bd-202x300.jpg\" alt=\"\" width=\"202\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/26223103/COACH_EDP_50ml-bd-202x300.jpg 202w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/26223103/COACH_EDP_50ml-bd-768x1138.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/26223103/COACH_EDP_50ml-bd-691x1024.jpg 691w\" sizes=\"(max-width: 202px) 100vw, 202px\" />\n<p><a href=\"https://www.myer.com.au/shop/mystore/all-fragrance/coach-453495070--1\">Coach EDP (50ml), $105.</a></p>\n<p>&nbsp;</p>\n</div>\n</div>\n</div>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/05/26220217/COACH_Men27s_Fragance_BTS_046.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"17d1666a-a387-55bc-8d8f-9b907f523483"}}

